import * as React from 'react';
import { Heading, Box, Text } from 'components/foundations';
import { Space } from 'components/Theme';
import styled from 'styled-components';
import { colors } from 'utils/variables';

interface DocsHeaderProps {
  title: string;
  subtitle?: string;
  margin?: Space;
}

const DocsHeader: React.FC<DocsHeaderProps> = ({ title, subtitle, margin }) => {
  return (
    <Section>
      <Box as="header" mb={margin}>
        <Heading as="h1" size={800} color="white" fontFamily="sansSerif">
          {title}
        </Heading>
        <Text as="p" size={400} color="grey04" mt="sm">
          {subtitle}
        </Text>
      </Box>
    </Section>
  );
};

const Section = styled('section')`
  padding: 1.25rem 1.875rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 200px;
  background-color: ${colors.grey07};
`;

DocsHeader.defaultProps = {
  subtitle: undefined,
  margin: 'xl'
};


export default DocsHeader;
